.example {
  background-color: $special_grey-3;
  padding-top: 1px;
  padding-bottom: 1px;
}

.example__wrapper {
  margin: 25px auto 40px auto;
}

.example__title {
  font-family: $font_oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: $black;
  text-align: start;
  margin: 0 0 40px 0;
}

.example__text {
  margin: 0 0 20px 0;
}

.example__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
}

.example__item {
  margin: 0;
  list-style: none;
}

.example__item-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 124px;
  padding-top: 15px;
}

.example__item-index {
  font-family: $font_oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: $black;
  margin: 0 0 8px 0;
}

.example__item-description {
  font-size: 12px;
  line-height: 12px;
  margin: 0;
  width: min-content;
  text-align: center;
}

.example__text-effect {
  margin: 20px 0 40px 0;
  font-family: $font_oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: $black;
}

.example__range-img-fat {
  display: block;
}

.example__range-img-skinny {
  display: none;
}

.example__range-img-fat-tablet {
  display: none;
}

.example__range-bar {
  display: flex;
  width: 210px;
  justify-content: space-between;
  margin: 0 auto;
  align-items: center;
}

.example__range-label-name {
  font-family: $font_oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: $black;
}

@media (min-width: $tablet-width) {
  .example__wrapper-text {
    display: flex;
    justify-content: space-between;
  }

  .example__text-effect {
    font-size: 20px;
    width: 174px;
  }

  .example__range-img-fat {
    display: none;
  }

  .example__range-img-fat-tablet {
    display: unset;
  }
}

@media (min-width: $desktop-width) {
  .example__wrapper {
    display: flex;
    justify-content: space-between;
  }

  .example__wrapper-part1 {
    width: 435px;
  }

  .example__wrapper-text {
    display: block;
  }
}
